export const getZodiacIconUrl = (zIndex: string|number = 4, order:number|string = '') => {
    return baseState.$gameImgUrl + `icons/t${order}_icon_${zIndex}.png`
}

export const getAllZodiacIconUrlList = () => {
    let arr:string[] = []
    for (let i = 0; i < 12; i++) {
        arr.push(getZodiacIconUrl(i, 3))
    }
    return arr
}

