const vWechatCodeDrag = {
    beforeMount(el, binding, vnode) {
        let oDiv = el;   //å½ååç´ 
        let disX = 0;
        let disY = 0;
        const isCopyQrCode = binding.value
        //é¼ æ æä¸
        const MouseDown = (e) => {
            disX = e.clientX - oDiv.offsetLeft;
            disY = e.clientY - oDiv.offsetTop;
            // console.log('e.clientX :>> ', e.clientX);
            // console.log('oDiv.offsetLeft :>> ', oDiv.offsetLeft)
            // console.log('é¼ æ æä¸', {x: disX, y: disY});
            document.body.addEventListener('mousemove', MouseMove);
            e.preventDefault();
        }

        //é¼ æ ç§»å¨
        const MouseMove = (e) => {
            //ç¨é¼ æ çä½ç½®åå»é¼ æ ç¸å¯¹åç´ çä½ç½®ï¼å¾å°åç´ çä½ç½®
            let left = e.clientX - disX;
            let top = e.clientY - disY;
            // console.log('å½åä½ç½®', {x: left, y: top})
            oDiv.style.left = left + 'px';
            oDiv.style.top = top + 'px';
            // SetOffset(left, top)
            e.preventDefault();
        }
        //é¼ æ å¼¹èµ·
        const MouseUp = () => {
            document.body.removeEventListener('mousemove', MouseMove);
        }

        oDiv.addEventListener('mousedown', MouseDown);
        oDiv.addEventListener('mouseup', MouseUp);
    },
}



